import React from 'react'

export default function Progbar({bgcolor,progress,height,text}) {



    const prog = {
        marginTop:'7vh',
        justifyContent:'center',
        alignItems:'center',
        display:'flex',
        width:'100%',
      }

      const prog2 = {
        width:'70vw',
      }


    const Parentdiv = {
        height: height,
        width: '100%',
        backgroundColor: 'whitesmoke',
        borderRadius: 40,
        margin: 10,
      }
      
      const Childdiv = {
        height: '100%',
        width: `${progress}%`,
        backgroundColor: bgcolor,
       borderRadius:40,
        textAlign: 'right'
      }
      
      const progresstext = {
        padding: 10,
        color: 'white',
        fontWeight: 700
      }
        
  return (
    <div style={prog}>
    <div style={prog2}>
     <h2>{`${text}`}</h2>
           <div style={Parentdiv}>
      <div style={Childdiv}>
      </div>
    </div>
    </div>
    </div>
  )
}
